// src/App.js
import React, { useState, useEffect } from 'react';
import { Routes, Route, useLocation } from 'react-router-dom';
import 'aos/dist/aos.css'; // Note: AOS is still imported if you haven't removed it.

// Helper Component
import ScrollToTop from './components/ScrollToTop'; // <-- 1. IMPORT our new component

// Layout
import Layout from './components/Layout';

// Page Components
import HomePage from './pages/HomePage';
import ServicesPage from './pages/ServicesPage';
import ShowcasePage from './pages/ShowcasePage';
import ContactPage from './pages/ContactPage';
import PrivacyPolicyPage from './pages/PrivacyPolicyPage';
import AiGuidesPage from './pages/AiGuidesPage';
import CaseStudiesPage from './pages/CaseStudiesPage';

function App() {
  const [isMenuOpen, setMenuOpen] = useState(false);
  const location = useLocation();
  
  // Close menu on route change
  useEffect(() => {
    handleCloseMenu();
  }, [location]);

  const handleMenuToggle = () => {
    setMenuOpen(!isMenuOpen);
    document.body.style.overflow = !isMenuOpen ? 'hidden' : '';
  };
  
  const handleCloseMenu = () => {
    setMenuOpen(false);
    document.body.style.overflow = '';
  };

  return (
    <Layout isMenuOpen={isMenuOpen} handleMenuToggle={handleMenuToggle} handleCloseMenu={handleCloseMenu}>
      <ScrollToTop /> {/* <-- 2. PLACE the new component here */}
      <Routes>
        <Route path="/" element={<HomePage />} />
        <Route path="/services" element={<ServicesPage />} />
        <Route path="/showcase" element={<ShowcasePage />} />
        <Route path="/contact" element={<ContactPage />} />
        <Route path="/privacy-policy" element={<PrivacyPolicyPage />} />
        <Route path="/ai-guides" element={<AiGuidesPage />} />
        <Route path="/case-studies" element={<CaseStudiesPage />} />
      </Routes>
    </Layout>
  );
}

export default App;