// src/components/AnimatedLogo.js
import React from 'react';
import { motion } from 'framer-motion';

// This is the "container" that will orchestrate the stagger effect
const logoContainerVariants = {
  animate: {
    transition: {
      staggerChildren: 0.1, // Each letter will start its animation 0.1s after the previous one
    },
  },
};

// This is the animation for each individual letter
const letterVariant = {
  animate: {
    y: [0, -8, 0], // The up-and-down motion
    transition: {
      duration: 2, // The total time for one full up-and-down cycle
      repeat: Infinity, // This makes the animation loop forever
      ease: "easeInOut",
    },
  },
};

const AnimatedLogo = () => {
  const logoText = "AxiomWeb";

  return (
    <motion.div
      className="logo" // We use the existing .logo class for styling
      variants={logoContainerVariants}
      initial="initial" // Although we don't define an 'initial', it's good practice
      animate="animate"   // This starts the animation automatically
    >
      {logoText.split('').map((char, index) => (
        <motion.span
          key={`${char}-${index}`}
          variants={letterVariant}
          style={{ display: 'inline-block' }}
        >
          {char}
        </motion.span>
      ))}
    </motion.div>
  );
};

export default AnimatedLogo;